@import url(//fonts.googleapis.com/css?family=Lato:300:400);
body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;text-align:center;background-color:var(--background-color)}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}#tools,#projects{padding-top:10vh}h1,h2{padding:1rem}@media (prefers-color-scheme: dark){:root .light{--color: #121212;--background-color: #fff;--link-color: rgb(0, 0, 238);--main-headline-color: #b75d69;--accent-background-color: #fff;--accent-color: rgba(17, 30, 108, 1);--wave1:rgba(255, 255, 255, 0.7);--wave2:rgba(255, 255, 255, 0.5);--wave3:rgba(255, 255, 255, 0.3);--wave4:rgba(255, 255, 255, 1);--card-box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2)}:root .dark{--color: #fff;--background-color: rgba(12, 21, 74, 1);--link-color: rgb(207, 218, 219);--main-headline-color: rgb(233, 30, 99);--accent-background-color:rgba(8, 14, 51, 1);--accent-color: #fff;--wave1:rgba(12, 21, 74, 0.3);--wave2:rgba(12, 21, 74, 0.5);--wave3:rgba(12, 21, 74, 0.7);--wave4:rgba(12, 21, 74, 1);--card-box-shadow: 0 4px 8px 0 rgba(0,0,0,0.8)}}@media (prefers-color-scheme: no-preference), (prefers-color-scheme: light){:root .light{--color: #121212;--background-color: #fff;--link-color: rgb(0, 0, 238);--main-headline-color: #b75d69;--accent-background-color: #fff;--accent-color: rgba(17, 30, 108, 1);--wave1:rgba(255, 255, 255, 0.7);--wave2:rgba(255, 255, 255, 0.5);--wave3:rgba(255, 255, 255, 0.3);--wave4:rgba(255, 255, 255, 1);--card-box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2)}:root .dark{--color: #fff;--background-color: rgba(12, 21, 74, 1);--link-color: rgb(207, 218, 219);--main-headline-color: rgb(233, 30, 99);--accent-background-color: rgba(8, 14, 51, 1);--accent-color: #fff;--wave1:rgba(12, 21, 74, 0.3);--wave2:rgba(12, 21, 74, 0.5);--wave3:rgba(12, 21, 74, 0.7);--wave4:rgba(12, 21, 74, 1);--card-box-shadow: 0 4px 8px 0 rgba(0,0,0,0.8)}}

.header{display:flex;flex-direction:column;justify-content:space-between;position:relative;background:linear-gradient(80deg, #2039cc 0%, #111e6c 100%);color:white;height:80vh}.header .greeting{text-align:left;font-family:'Lato', sans-serif;font-weight:300;letter-spacing:2px;font-size:48px}.header h1{text-align:left;font-family:'Lato', sans-serif;font-weight:300;letter-spacing:2px;font-size:32px}.header .name{font-weight:600}.header h2{text-align:left;font-family:'Lato', sans-serif;font-weight:300;letter-spacing:2px;font-size:18px;color:white;padding:0 1rem;opacity:0.9}.waves{min-height:80px}.parallax>use{-webkit-animation:move-forever 25s cubic-bezier(0.55, 0.5, 0.45, 0.5) infinite;animation:move-forever 25s cubic-bezier(0.55, 0.5, 0.45, 0.5) infinite}.parallax>use:nth-child(1){-webkit-animation-delay:-2s;animation-delay:-2s;-webkit-animation-duration:7s;animation-duration:7s}.parallax>use:nth-child(2){-webkit-animation-delay:-3s;animation-delay:-3s;-webkit-animation-duration:10s;animation-duration:10s}.parallax>use:nth-child(3){-webkit-animation-delay:-4s;animation-delay:-4s;-webkit-animation-duration:13s;animation-duration:13s}.parallax>use:nth-child(4){-webkit-animation-delay:-5s;animation-delay:-5s;-webkit-animation-duration:20s;animation-duration:20s}@-webkit-keyframes move-forever{0%{transform:translate3d(-90px, 0, 0)}100%{transform:translate3d(85px, 0, 0)}}@keyframes move-forever{0%{transform:translate3d(-90px, 0, 0)}100%{transform:translate3d(85px, 0, 0)}}@media (min-width: 768px){.header .waves{height:15vh}.header .greeting,.header h1{font-size:72px}.header h2{font-size:28px}.header .title{margin:0 auto}}

.Switch{display:flex;justify-content:flex-end;margin:20px}.toggleWrapper input{position:relative;opacity:0}.toggle{cursor:pointer;display:inline-block;position:relative;width:80px;height:40px;background-color:#83D8FF;border-radius:84px;transition:background-color 200ms cubic-bezier(0.445, 0.05, 0.55, 0.95)}.toggle__handler{display:inline-block;position:relative;z-index:1;top:3px;left:-20px;width:34px;height:34px;background-color:#f5f05b;border-radius:40px;box-shadow:0 2px 6px rgba(0,0,0,0.3);transition:all 400ms cubic-bezier(0.68, -0.55, 0.265, 1.55);transform:rotate(-45deg)}.toggle__handler .crater{position:absolute;background-color:#E8CDA5;opacity:0;transition:opacity 200ms ease-in-out;border-radius:100%}.toggle__handler .crater--1{top:16px;left:8px;width:4px;height:4px}.toggle__handler .crater--2{top:24px;left:18px;width:6px;height:6px}.toggle__handler .crater--3{top:8px;left:20px;width:8px;height:8px}.star{position:absolute;background-color:#ffffff;transition:all 300ms cubic-bezier(0.445, 0.05, 0.55, 0.95);border-radius:50%}.star--1{top:8px;left:24px;z-index:0;width:30px;height:3px}.star--2{top:16px;left:24px;z-index:1;width:30px;height:3px}.star--3{top:24px;left:36px;z-index:0;width:30px;height:3px}.star--4,.star--5,.star--6{opacity:0;transition:all 300ms 0 cubic-bezier(0.445, 0.05, 0.55, 0.95)}.star--4{top:16px;left:8px;z-index:0;width:2px;height:2px;transform:translate3d(3px, 0, 0)}.star--5{top:24px;left:12px;z-index:0;width:3px;height:3px;transform:translate3d(3px, 0, 0)}.star--6{top:30px;left:22px;z-index:0;width:2px;height:2px;transform:translate3d(3px, 0, 0)}input:checked+.toggle{background-color:#080E33}input:checked+.toggle:before{color:#749ED7}input:checked+.toggle:after{color:#ffffff}input:checked+.toggle .toggle__handler{background-color:#FFE5B5;transform:translate3d(40px, 0, 0) rotate(0)}input:checked+.toggle .toggle__handler .crater{opacity:1}input:checked+.toggle .star--1{width:2px;height:2px}input:checked+.toggle .star--2{width:4px;height:4px;transform:translate3d(-5px, 0, 0)}input:checked+.toggle .star--3{width:2px;height:2px;transform:translate3d(-7px, 0, 0)}input:checked+.toggle .star--4,input:checked+.toggle .star--5,input:checked+.toggle .star--6{opacity:1;transform:translate3d(0, 0, 0)}input:checked+.toggle .star--4{transition:all 300ms 200ms cubic-bezier(0.445, 0.05, 0.55, 0.95)}input:checked+.toggle .star--5{transition:all 300ms 300ms cubic-bezier(0.445, 0.05, 0.55, 0.95)}input:checked+.toggle .star--6{transition:all 300ms 400ms cubic-bezier(0.445, 0.05, 0.55, 0.95)}

#tools{text-align:center;background-color:var(--background-color);color:var(--color)}#tools .name{font-weight:600;font-size:36px}#tools .list{margin:0 auto;width:80vw;display:grid;grid-gap:2rem;gap:2rem;align-items:center;justify-content:center;grid-template-columns:repeat(3, 48px)}@media (min-width: 576px){#tools .list{grid-template-columns:repeat(4, 40px)}}@media (min-width: 768px){#tools .list{max-width:920px;margin:0 auto;display:grid;align-items:center;grid-template-columns:repeat(6, 48px)}}

#projects{background-color:var(--background-color)}#projects .name{font-weight:600;font-size:36px}h2{font-size:32px;color:var(--color);padding:2rem}.bold{font-weight:600}.date{opacity:0.6;font-feature-settings:"c2sc";font-variant:small-caps;color:var(--accent-color)}.projects-list{-ms-scroll-snap-type:x mandatory;scroll-snap-type:x mandatory;overflow-x:scroll;overflow-y:hidden;display:flex;align-items:center;-webkit-overflow-scrolling:touch}.projects-list .card{scroll-snap-align:center;scroll-snap-stop:always;border-radius:10px;margin:20px;width:250px;height:250px;box-shadow:var(--card-box-shadow);transition:0.3s;color:var(--color);background-color:var(--accent-background-color);display:grid;grid-template-rows:1fr 1fr 1fr}.projects-list .card p{align-self:center}.projects-list .card .testlogo{width:64px;margin:auto;padding-top:20px}.projects-list .card .projectName{margin:auto}.projects-list .card .projectDate{margin:auto}@media (min-width: 992px){.projects-list{display:grid;grid-gap:2rem;justify-content:center;margin:0 auto;grid-template-columns:repeat(auto-fit, 250px);overflow-x:hidden;max-width:920px}.projects-list .item{margin:0;place-self:center}}

.wrapper{margin:0 auto;position:relative;background:var(--background-color);justify-content:center;width:250px;height:250px}.waveLoader{position:relative;width:250px;height:250px}.waveLoader:before,.waveLoader:after{position:absolute;content:'';border:4px solid var(--color);opacity:1;top:50%;left:50%;border-radius:50%;transform:translate(-50%, -50%);-webkit-animation:wave 1.2s cubic-bezier(0, 0.2, 0.8, 1) infinite;animation:wave 1.2s cubic-bezier(0, 0.2, 0.8, 1) infinite}.waveLoader:after{-webkit-animation-delay:-0.4s;animation-delay:-0.4s}@-webkit-keyframes wave{0%{width:0;height:0;opacity:1}100%{width:100%;height:100%;opacity:0}}@keyframes wave{0%{width:0;height:0;opacity:1}100%{width:100%;height:100%;opacity:0}}

#contact{color:var(--color);padding:10vh 0 5vh;margin-right:auto;margin-left:auto;width:80vw;display:grid;grid-gap:1rem;gap:1rem;align-items:center;justify-content:center;grid-template-columns:repeat(5, 32px)}#contact .item svg{width:32px;height:32px}

#footer{margin:auto;height:10vh;background-color:var(--background-color);color:var(--color);text-align:center;align-items:center;display:grid}

